.scrolled {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 0.1s, opacity 0.1s linear;
}

.navbar {
  position: fixed;
  width: 100%;
  z-index: 3;
  box-sizing: border-box;
  box-shadow: 0.5px 0.5px 1.5px black;
}

.navbar__linksBox {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 2vw 0 2vw;
  background-image: linear-gradient(125deg, #252525, #313131);
}

.navbar__linksBox--links:hover {
  text-shadow: 1.5px 1.5px 5px white;
}

.navbar__linksBox--links {
  padding: 10px;
  justify-content: flex-start;
  color: whitesmoke;
  text-decoration: none;
  background-color: rgba(0, 0, 0, 0);
  text-shadow: 0.5px 0.25px grey;
}

.navbar__linksBox--dropMenuBtn:hover {
  text-shadow: 1.5px 1.5px 5px white;
}

.navbar__linksBox--dropMenuBtn {
  border: none;
  padding: 10px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  color: whitesmoke;
  outline: none;
  font-size: inherit;
}

.navbar__linksBox--dropMenuBtn__container {
  display: flex;
  flex-direction: column;
  position: absolute;
  border: none;
  box-shadow: 1px 1px 1px gray;
  top: 100%;
  left: 83vw;
  border-radius: 0 0 5px 5px;
  overflow: hidden;
}

.navbar__linksBox--dropMenuBtn__container--button:hover {
  background-image: linear-gradient(to top right, rgba(86, 57, 153, 0.75), rgba(30, 115, 172, 0.75));
  color: white;
}

.navbar__linksBox--dropMenuBtn__container--button {
  text-align: start;
  font-size: 15px;
  padding: 10px;
  width: 15vw;
  border: none;
  cursor: pointer;
  background-color: whitesmoke;
  text-shadow: 0.5px 0.5px 1px grey;
}
