.projectInfo {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
}

.projectInfo__linkBox {
  overflow: hidden;
}

.projectInfo__linkBox--link {
  cursor: default;
  overflow: inherit;
  display: flex;
  justify-content: center;
  overflow: hidden;
}

.projectInfo__linkBox--link__img {
  background-image: url(/static/media/code.4bffe307.jpg);
  width: 100%;
  height: 98vh;
  object-fit: cover;
  overflow: hidden;
  border-radius: 5px;
}

.projectInfo__linkBox--link__header {
  position: absolute;
  left: 1%;
  top: 5%;
  font-size: 24px;
  font-weight: bold;
  color: white;
  text-shadow: 1px 1px 2px grey;
}

.projectInfo__textSection {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 10px 1vw;
  background-size: cover;
  background-position: center;
  background-image: linear-gradient(to bottom, #e4d8e1, #98a4ce);
}

.projectInfo__textSection--row {
  width: 50%;
}

.projectInfo__textSection--row__text {
  text-shadow: 0.5px 0.25px grey;
}

.projectInfo__textSection--row__link:hover {
  color: white;
  box-shadow: 10px 0 0 orangered, -10px 0 0 orangered;
  background-color: orangered;
}

.projectInfo__textSection--row__link {
  font-weight: bold;
  color: black;
  text-shadow: 0.5px 0.25px grey;
  text-decoration: none;
  box-shadow: 10px 0 0 orange, -10px 0 0 orange;
  background-color: orange;
}

.projectInfo__textSection--imgBox {
  padding-left: 10px;
}

.projectInfo__textSection--imgBox__img {
  width: 7vw;
  background-color: transparent;
  margin: 0 5px;
}
